.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* by me */
.btn{
  padding: 10px 15px;
  border-radius: 4px;
  border: none;
  color: #fff;
  margin: 10px;
}
.danger{
  background: red;
}
.primary{
  background: rgb(2, 133, 150);
}
.success{
  background: rgb(0, 131, 0);
}